.profile__avatar-button {
  background-image: url(../../../images/edit-avatar.svg);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  position: absolute;
  opacity: 0;
  border: 0;
  background-repeat: no-repeat;
  background-position: center;
  transition: .8s ease;
  cursor: pointer;
}

.profile__avatar-button:hover {
  opacity: .8;
  background-color: #000;
}
