.popup-register__btn-close {
  width: 32px;
  height: 32px;
  border: none;
  background-image: url(../../../images/close.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: -32px;
  left: 100%;
  cursor: pointer;
}